





































































import { NameDialog } from '@/models/dialog.models'
import { Component, Prop, Vue, Watch } from 'vue-property-decorator'

@Component({})
export default class NameDialogComponent extends Vue {
  @Prop({ default: false }) loading: boolean
  @Prop() dialog: NameDialog

  name = ''

  get title(): string {
    return this.dialog.title
  }

  get placeHolderText(): string {
    return this.dialog.placeHolderText
  }

  get buttonText(): string {
    return this.dialog.buttonText
  }

  get buttonColor(): string {
    return this.dialog.buttonColor
  }

  get helperText(): string {
    return this.dialog.helperText
  }

  get saveButtonEnabled(): boolean {
    return this.name.trim() !== '' || this.loading
  }

  @Watch('dialog')
  onOpenChange(dialog: NameDialog): void {
    this.name = ''
    if (dialog && dialog.name) {
      this.name = dialog.name
    }
  }

  closeDialog(): void {
    this.$emit('update:dialog', null)
  }

  onSubmit(): void {
    this.$emit('onSubmit', this.name)
  }
}
