
























































































































import { Component, Prop, Vue } from 'vue-property-decorator'
import { ViewTreeNodeModel, TreeNode } from '@/models/hierarchy.models'
import { OrgUnitsDrawerModel } from '@/models/drawer.models'
import LoadingSpinner from '@/components/shared/LoadingSpinner.vue'

interface TreeViewShim extends Element {
  updateAll: (val: boolean) => void
}

@Component({
  components: { LoadingSpinner }
})
export default class OrgUnitsDrawer extends Vue {
  @Prop() orgUnitsDrawer: OrgUnitsDrawerModel
  @Prop({ default: () => [] }) orgUnits: ViewTreeNodeModel[]
  @Prop() orgUnitId: string
  @Prop() saving: boolean
  @Prop() loading: boolean

  selectedOrgUnitId: string = null
  search = ''

  get text(): string {
    return (this.orgUnitsDrawer && this.orgUnitsDrawer.text) || ''
  }

  get buttonText(): string {
    return (this.orgUnitsDrawer && this.orgUnitsDrawer.buttonText) || ''
  }

  get selectedOrgUnitName(): string {
    if (!this.selectedOrgUnitId) {
      return null
    } else {
      return this.orgUnits.find(orgUnit => orgUnit.nodeId === this.selectedOrgUnitId).nodeName
    }
  }

  get mappedOrgUnits(): TreeNode[] {
    return this.orgUnits.map((orgUnit) => {
      return {
        id: orgUnit.nodeId,
        parentId: orgUnit.parentNodeId,
        name: orgUnit.nodeName,
        children: []
      } as TreeNode
    }).sort((a, b) => {
      // ensure sort by id so rootChild will be root node
      return a.id.length - b.id.length
    })
  }

  get rootChild(): TreeNode {
    return this.mappedOrgUnits && this.mappedOrgUnits[0]
  }

  get treeData(): TreeNode[] {
    if (!this.rootChild) {
      return []
    }
    return [{
      ...this.rootChild,
      children: this.getNestedChildren(this.mappedOrgUnits, this.rootChild.id)
    }]
  }

  closeClick(): void {
    this.onInput(false)
  }

  onInput(open: boolean): void {
    this.$emit('update:orgUnitsDrawer', open)
    if (!open) {
      this.selectedOrgUnitId = null
      this.search = ''
    }
  }

  searchInput(val: string): void {
    const tree = this.$refs.tree as TreeViewShim
    if (val && val !== '') {
      tree.updateAll(true)
    } else {
      tree.updateAll(false)
    }
  }

  getNestedChildren(arr: TreeNode[], parent: string): TreeNode[] {
    const out = []
    arr.forEach((node) => {
      if (node.parentId === parent) {
        const children = this.getNestedChildren(arr, node.id)
        if (children.length) {
          node.children = children
        }
        out.push(node)
      }
    })
    return out
  }

  labelClass(name: string): any {
    return {
      highlight: this.search && name.toLowerCase().indexOf(this.search.toLocaleLowerCase()) > -1
    }
  }

  selectOrgUnits(): void {
    this.$emit('selectOrgUnits', this.selectedOrgUnitId)
  }
}
