// vuex-persist docs: https://github.com/championswimmer/vuex-persist#readme
import VuexPersist from 'vuex-persist'

const orgUnitsPersist = new VuexPersist({
  key: 'Echelon.OrgUnits',
  storage: window.sessionStorage,
  reducer: ({ OrgUnits }) => ({
    OrgUnits: {
      expandedIds: OrgUnits.expandedIds
    }
  })
})

// add persist for other modules with their own config here

export {
  orgUnitsPersist
  // export additional module persists here
}
