










import OrgChart from '@/lib/orgchart.js'
import { OrgChartNode } from '@/models/hierarchy.models'
import { DEFAULT_TEMPLATE, SVG_SELECTOR } from '@/components/org-chart/orgChart.template.factory'
import { Component, Vue, Watch, Prop } from 'vue-property-decorator'

interface OrgChartEventArgs {
  node: OrgChartNode,
  event: Event
}

@Component({})
export default class OrgChartDesigner extends Vue {
  @Prop() nodes: OrgChartNode[]
  @Prop() editingDisabled: boolean
  @Prop() hierarchyRoot: string
  @Prop({ required: false }) leafLabel: string
  @Prop({ default: () => [] }) expandedIds: string[]

  chart: OrgChart = {} as OrgChart
  public chartInit = false
  resizeTimer = null

  @Watch('nodes', { immediate: true, deep: true })
  async onOrgChartChanged(): Promise<void> {
    if (this.chartInit) {
      this.reloadChart()
    } else {
      await this.$nextTick()
      this.initChart()
    }
    this.expandNodes()
  }

  destroyed(): void {
    this.chart.destroy()
    this.chart = {}
  }

  initChart(): void {
    const domEl = this.$refs.tree as HTMLElement
    const config = {
      template: DEFAULT_TEMPLATE,
      nodes: this.nodes,
      nodeBinding: {
        name: 'name',
        childCount: 'childCount',
        workgroupsCount: 'workgroupsCount'
      },
      layout: OrgChart.tree,
      scaleInitial: 1,
      toolbar: {
        fit: true
      },
      nodeMouseClick: OrgChart.action.none,
      collapse: {
        level: 2,
        allChildren: true
      },
      enableDragDrop: true,
      nodeMenu: {
        addOrgUnit: {
          text: 'Add New OrgUnit',
          icon: OrgChart.icon.add(24, 24, '#7A7A7A'),
          onClick: this.handleAddOrgUnit
        },
        removeOrgUnit: {
          text: 'Remove OrgUnit',
          icon: OrgChart.icon.remove(24, 24, '#7A7A7A'),
          onClick: this.handleRemoveOrgUnit
        }
      }
    }
    if (this.editingDisabled) {
      config.enableDragDrop = false
      delete config.nodeMenu
    }
    this.chart = new OrgChart(domEl, config)
    this.chart.on('click', this.nodeClick)
    this.chart.on('drop', this.handleNodeDrop)
    this.chart.on('expcollclick', this.setExpandedIds)
    this.chart.on('searchclick', this.searchClick)
    this.chart.on('redraw', (): void => {
      this.chartInit = true
    })
    setTimeout(() => {
      this.chart.fit()
    }, 0)
  }

  reloadChart(): void {
    // to keep in sync with data from server, we reload chart nodes when changes occur
    // it looks weird if we show the expand animation everytime
    // temporarily remove animation prior to load, then restore it
    const duration = this.chart.config.anim.duration
    this.chart.config.anim.duration = 1
    this.chart.load(this.nodes)
    // can't use $nextTick() because the svg stuff is not Vue reactive
    setTimeout(() => {
      this.chart.config.anim.duration = duration
    }, 0)
  }

  handleNodeDrop(sender: OrgChart, draggedNodeId: string, droppedNodeId: string): unknown {
    if ((!draggedNodeId || !droppedNodeId) || droppedNodeId.startsWith(draggedNodeId)) {
      return false
    }
    this.$emit('moveOrgUnit', draggedNodeId, droppedNodeId)
    return false
  }

  async setExpandedIds(sender: OrgChart, collapsing: boolean, id: string, ids: string[]): Promise<void> {
    if (collapsing) {
      this.$emit('collapseNodes', id)
    } else {
      this.$emit('expandNodes', ids)
    }
    await this.$nextTick()
    this.chart.center(id)
  }

  // We can use this method for event delegation to do different stuff based on clicked target.
  nodeClick(sender: OrgChart, args: OrgChartEventArgs): void {
    const { event, node } = args
    const { id } = node
    const target = event.target as Element
    const { dataset } = target
    const { route } = dataset
    this.$emit('orgUnitClick', id, route)
  }

  public expandNodes(centerOnId?: string): void {
    this.chart.expand(this.hierarchyRoot, this.expandedIds)
    if (centerOnId) {
      setTimeout(() => {
        this.chart.center(centerOnId)
      }, this.chart.config.anim.duration)
    }
  }

  async searchClick(sender: OrgChart, nodeId: string): Promise<void> {
    await this.$nextTick()
    const nodeEl = document.querySelector(`[node-id="${nodeId}"]`)
    if (!nodeEl) {
      return
    }
    nodeEl.classList.add('highlight')
    this.expandNodePathById(nodeId)
  }

  expandNodePathById(nodeId: string): void {
    const arr = nodeId.substring(this.hierarchyRoot.length).split('/')
    const nodeIds = []
    let next = this.hierarchyRoot
    arr.forEach((level) => {
      if (level) {
        next = `${next}${level}/`
        nodeIds.push(next)
      }
    })
    this.$emit('expandNodes', nodeIds)
  }

  handleAddOrgUnit(parentNodeId: string): void {
    this.$emit('onAddNewOrgUnit', parentNodeId)
  }

  handleRemoveOrgUnit(nodeId: string): void {
    this.$emit('onRemoveOrgUnit', nodeId)
  }

  onResize(): void {
    // forces chart to fit on screen resize
    clearTimeout(this.resizeTimer)
    this.resizeTimer = setTimeout(() => {
      const svg = document.getElementById(SVG_SELECTOR)
      if (svg) {
        svg.style.height = '0'
        this.chart.fit()
        svg.style.height = null
      }
    }, 350)
  }
}
