import Vue from 'vue'
import VueRouter, { RouteConfig } from 'vue-router'
import { OrgUnitDataTypes } from '@/shared/constants/enum'
import Home from '@/views/Home.vue'
import OrgUnitDetail from '@/views/OrgUnitDetail.vue'
import WorkgroupsTab from '@/components/org-unit/WorkgroupsTab.vue'
import ChildrenTab from '@/components/org-unit/ChildrenTab.vue'

Vue.use(VueRouter)

export enum RouteName {
  home = 'home',
  orgunit = 'orgunit',
  workgroups = 'workgroups',
  children = 'children'
}

const routes: Array<RouteConfig> = [
  {
    path: '/',
    name: RouteName.home,
    component: Home
  },
  {
    path: '/orgunit',
    name: RouteName.orgunit,
    component: OrgUnitDetail,
    redirect: 'orgunit/children',
    beforeEnter(to, from, next): void {
      const { query } = to
      const { hierarchyId, orgUnitId } = query
      if (!hierarchyId || !orgUnitId) {
        next({ name: RouteName.home })
      }
      next()
    },
    children: [
      {
        path: 'children',
        name: RouteName.children,
        component: ChildrenTab,
        meta: {
          type: OrgUnitDataTypes.Children
        }
      },
      {
        path: 'workgroups',
        name: RouteName.workgroups,
        component: WorkgroupsTab,
        meta: {
          type: OrgUnitDataTypes.Workgroups
        }
      }
    ]
  },
  {
    path: '*',
    redirect: { name: RouteName.home }
  }
]

const router = new VueRouter({
  routes
})

export default router
