





















import { namespace } from 'vuex-class'
import { Component, Vue, Watch, Prop } from 'vue-property-decorator'
import {
  ACTION_CLEAR_SELECTED_WORKGROUPS,
  ACTION_FETCH_WORKGROUPS_FOR_ORG_UNIT,
  ACTION_SET_SELECTED_WORKGROUPS
} from '@/store/action-types'
import { WorkgroupViewModel } from '@/models/hierarchy.models'

const orgUnit = namespace('OrgUnit')

@Component({})
export default class WorkgroupsTab extends Vue {
  @Prop() editable: boolean

  loading = false

  headers = [
    { text: 'Name', value: 'workgroupName' },
    { text: 'Assigned', value: 'assignedDate' }
  ]

  selectedWorkgroups: WorkgroupViewModel[] = []

  @orgUnit.State
  workgroups: WorkgroupViewModel[]

  @orgUnit.Getter
  selectedOrgUnitId: string

  @orgUnit.Action
  [ACTION_FETCH_WORKGROUPS_FOR_ORG_UNIT]: (orgUnitId: string) => WorkgroupViewModel[]

  @orgUnit.Action
  [ACTION_SET_SELECTED_WORKGROUPS]: (selectedWorkgroups: WorkgroupViewModel[]) => void

  @orgUnit.Action
  [ACTION_CLEAR_SELECTED_WORKGROUPS]: () => void

  @Watch('selectedOrgUnitId', { immediate: true })
  onOrgUnitIdChang(): void {
    this.fetchWorkgroups()
  }

  destroyed(): void {
    this[ACTION_CLEAR_SELECTED_WORKGROUPS]()
  }

  async fetchWorkgroups(): Promise<void> {
    try {
      this.loading = true
      await this[ACTION_FETCH_WORKGROUPS_FOR_ORG_UNIT](this.selectedOrgUnitId)
    } catch (e) {
      console.error(e)
    } finally {
      this.loading = false
    }
  }

  setSelectedWorkgroups(selectedWorkgroups: WorkgroupViewModel[]): void {
    this[ACTION_SET_SELECTED_WORKGROUPS](selectedWorkgroups)
  }
}
