import _Vue from 'vue'
import axios, { AxiosInstance } from 'axios'
import VueCookies from 'vue-cookies-ts'
_Vue.use(VueCookies)

export interface Api {
  hierarchy: AxiosInstance
}

const httpPlugin: Api = {
  hierarchy: axios.create({
    baseURL: process.env.VUE_APP_API_URL,
    headers: {
      Authorization: `Bearer ${_Vue.cookies.get('authToken')}`,
      'Content-Type': 'application/json'
    }
  })
}

httpPlugin.hierarchy.interceptors.response.use(res => res, error => {
  if ([401].includes(error.response.status)) {
    const currentUrl = window.location.href
    window.location.href = `${process.env.VUE_APP_LOGIN_URL}?redirectUrl=${currentUrl}`
  }
})

function HttpPlugin(Vue: typeof _Vue): void {
  Vue.prototype.$http = httpPlugin
}

export { httpPlugin as $http, HttpPlugin }
