



















































import { Component, Prop, Vue } from 'vue-property-decorator'
import { ConfirmDialog } from '@/models/dialog.models'

@Component({})
export default class ConfirmDialogComponent extends Vue {
  @Prop({ default: null }) confirmDialog: ConfirmDialog
  @Prop({ default: false }) loading: boolean

  get title(): string {
    return this.confirmDialog.title
  }

  get text(): string {
    return this.confirmDialog.text
  }

  get buttonText(): string {
    return this.confirmDialog.buttonText
  }

  get buttonColor(): string {
    return this.confirmDialog.buttonColor
  }

  closeDialog(): void {
    this.$emit('update:confirmDialog', null)
  }

  confirm(): void {
    this.$emit('onConfirm')
  }
}
