

























































































import { OrgUnitDataTypes } from '@/shared/constants/enum'
import { Component, Prop, Vue } from 'vue-property-decorator'

interface ToolbarItem {
  name: string
  icon: string
  type?: number,
  isSelectionAction?: boolean
  description?: string
  children?: ToolbarItem[]
  action?: string
}
@Component({})
export default class OrgUnitToolbar extends Vue {
  @Prop({ default: false }) showSelectionActions: boolean
  @Prop() itemLabel: string
  @Prop() showItemsForType: number

  selectedItem: ToolbarItem = null

  get toolbar(): ToolbarItem[] {
    return [
      {
        name: 'Add New OrgUnit',
        icon: 'mdi-plus',
        type: OrgUnitDataTypes.Children,
        action: 'addOrgUnit'
      },
      {
        name: 'Move Selected Children',
        icon: 'mdi-application-export',
        isSelectionAction: true,
        type: OrgUnitDataTypes.Children,
        children: [
          {
            name: 'Move Selected Children',
            icon: 'mdi-sitemap',
            description: `Select this option to move selected children, their ${this.itemLabel} and children, to another org unit.`,
            action: 'moveChildren'
          },
          {
            name: `Move Selected Childrens ${this.itemLabel}`,
            icon: 'mdi-text-box-multiple-outline',
            description: `Select this option to move selected childrens ${this.itemLabel} only to another org unit.`,
            action: 'moveChildrenWorkgroups'
          }
        ]
      },
      {
        name: `Assign ${this.itemLabel}`,
        icon: 'mdi-application-import',
        type: OrgUnitDataTypes.Workgroups,
        action: 'assignWorkgroups'
      },
      {
        name: `Move Selected ${this.itemLabel}`,
        icon: 'mdi-application-export',
        isSelectionAction: true,
        type: OrgUnitDataTypes.Workgroups,
        action: 'moveWorkgroups'
      },
      {
        name: `Un-assign Selected ${this.itemLabel}`,
        icon: 'mdi-clipboard-remove-outline',
        isSelectionAction: true,
        type: OrgUnitDataTypes.Workgroups,
        action: 'unAssignWorkgroups'
      }
    ]
  }

  get filteredToolbar(): ToolbarItem[] {
    return this.toolbar
      .filter(item => item.type === this.showItemsForType)
      .filter(item => !item.isSelectionAction || (this.showSelectionActions && item.isSelectionAction))
  }

  async itemClick(item: ToolbarItem): Promise<void> {
    if (item.action) {
      this.$emit(item.action)
    }
    await this.$nextTick()
    this.selectedItem = null
  }
}
