

























import { Component, Vue, Watch } from 'vue-property-decorator'
import {
  AddOrgUnitModel,
  MoveOrgUnitTreeModel,
  OrgChartNode,
  RemoveOrgUnitModel,
  ViewTreeNodeModel
} from '@/models/hierarchy.models'
import { namespace } from 'vuex-class'
import {
  ACTION_ADD_EXPANDED_IDS,
  ACTION_ADD_ORG_UNIT,
  ACTION_CLEAR_EXPANDED_IDS,
  ACTION_DELETE_ORG_UNIT,
  ACTION_FETCH_ORG_UNITS,
  ACTION_MOVE_ORG_UNITS,
  ACTION_REMOVE_EXPANDED_IDS
} from '@/store/action-types'
import { OrganizationStatus } from '@/shared/constants/enum'
import OrgChartDesigner from '@/components/org-chart/OrgChartDesigner.vue'
import NameDialogComponent from '@/components/shared/NameDialogComponent.vue'
import { NameDialog } from '@/models/dialog.models'
import { RouteName } from '@/router'

const orgUnits = namespace('OrgUnits')
const organization = namespace('Organization')

@Component({
  components: {
    NameDialogComponent,
    OrgChartDesigner
  }
})
export default class OrgUnits extends Vue {
  saving = false
  loading = false
  editingParentOrgUnit: ViewTreeNodeModel = null
  addOrgUnitDialog = null

  @orgUnits.State
  expandedIds: string[]

  @organization.Getter
  selectedHierarchyId: string

  @organization.Getter
  leafLabel: string

  @organization.Getter
  selectedOrgStatus: number

  @orgUnits.State
  orgUnits: ViewTreeNodeModel[]

  @orgUnits.Getter
  orgChartNodes: OrgChartNode[]

  @orgUnits.Action
  [ACTION_FETCH_ORG_UNITS]: () => void

  @orgUnits.Action
  [ACTION_MOVE_ORG_UNITS]: (params: MoveOrgUnitTreeModel) => Promise<ViewTreeNodeModel[]>

  @orgUnits.Action
  [ACTION_ADD_ORG_UNIT]: (params: AddOrgUnitModel) => Promise<ViewTreeNodeModel>

  @orgUnits.Action
  [ACTION_DELETE_ORG_UNIT]: (params: RemoveOrgUnitModel) => Promise<string[]>

  @orgUnits.Action
  [ACTION_ADD_EXPANDED_IDS]: (ids: string[]) => void

  @orgUnits.Action
  [ACTION_REMOVE_EXPANDED_IDS]: (ids: string[]) => void

  @orgUnits.Action
  [ACTION_CLEAR_EXPANDED_IDS]: () => void

  get showOrgChartDesigner(): boolean {
    return this.selectedHierarchyId && this.orgChartNodes && this.orgChartNodes.length > 0
  }

  get orgChartDesignerDisabled(): boolean {
    return this.selectedOrgStatus === OrganizationStatus.Active
  }

  @Watch('selectedHierarchyId', { immediate: true })
  async onSelectedHierarchyIdChange(code: string, previousCode: string): Promise<void> {
    // Todo: pass chartInit as prop to orgChartDesigner, $emit changes to orgUnits
    const orgChartDesigner = this.$refs.orgChartDesigner as OrgChartDesigner
    if (orgChartDesigner && orgChartDesigner.chartInit) {
      orgChartDesigner.chartInit = false
    }
    if (previousCode && previousCode !== code) {
      this[ACTION_CLEAR_EXPANDED_IDS]()
    }
    try {
      this.$emit('update:loading', true)
      await this[ACTION_FETCH_ORG_UNITS]()
    } catch (e) {
      console.error(e)
    } finally {
      this.$emit('update:loading', false)
    }
  }

  orgUnitClick(orgUnitId: string, route: RouteName): void {
    const query = {
      hierarchyId: this.selectedHierarchyId,
      orgUnitId
    }
    if (route && route === RouteName.children) {
      this.$router.push({ name: RouteName.children, query })
    } else if (route && route === RouteName.workgroups) {
      this.$router.push({ name: RouteName.workgroups, query })
    }
  }

  async moveOrgUnit(draggedNodeId: string, droppedNodeId: string): Promise<void> {
    const nodesToCollapse = [draggedNodeId].concat(this.getChildNodeIdsByParentId(draggedNodeId))
    const { pid } = this.orgChartNodes.find(node => node.id === draggedNodeId)
    const params = {
      hierarchyId: this.selectedHierarchyId,
      oldParentNodeId: pid,
      newParentNodeId: droppedNodeId,
      orgUnits: [draggedNodeId]
    } as MoveOrgUnitTreeModel
    try {
      this.$emit('update:loading', true)
      const updatedNodeSection = await this[ACTION_MOVE_ORG_UNITS](params)
      const nodesToExpand = updatedNodeSection.filter(node => node.parentNodeId === droppedNodeId).map(node => node.nodeId)
      this.collapseNodes(nodesToCollapse)
      this.expandNodes(nodesToExpand)
      const orgChartDesigner = this.$refs.orgChartDesigner as OrgChartDesigner
      orgChartDesigner.expandNodes(updatedNodeSection[0].nodeId)
    } catch (e) {
      console.error(e)
    } finally {
      this.$emit('update:loading', false)
    }
  }

  expandNodes(ids: string[]): void {
    this[ACTION_ADD_EXPANDED_IDS](ids)
  }

  collapseNodes(ids: string[]): void {
    this[ACTION_REMOVE_EXPANDED_IDS](ids)
  }

  collapseNodesByParentId(parentId: string): void {
    this.collapseNodes(this.getChildNodeIdsByParentId(parentId))
  }

  getChildNodeIdsByParentId(parentId: string): string[] {
    return this.orgChartNodes.filter((node) => {
      const pid = node.pid as string
      return pid.startsWith(parentId)
    }).map((node) => {
      return node.id as string
    })
  }

  onAddNewOrgUnit(parentId: string): void {
    this.editingParentOrgUnit = this.orgUnits.find(node => node.nodeId === parentId)
    const params = {
      title: 'Add New Org Unit',
      placeHolderText: 'New Org Unit Name',
      helperText: `will be added to <strong>${this.editingParentOrgUnit.nodeName}</strong>`,
      buttonText: 'Add Org Unit'
    } as NameDialog
    this.addOrgUnitDialog = new NameDialog(params)
  }

  async addOrgUnit(orgUnitName: string): Promise<void> {
    const params = {
      orgUnitName,
      parentOrgUnitId: this.editingParentOrgUnit.nodeId
    } as AddOrgUnitModel
    try {
      this.saving = true
      const newOrgUnit = await this[ACTION_ADD_ORG_UNIT](params)
      this.addOrgUnitDialog = null
      this.editingParentOrgUnit = null
      const orgChartDesigner = this.$refs.orgChartDesigner as OrgChartDesigner
      this.expandNodes([newOrgUnit.nodeId])
      orgChartDesigner.expandNodes(newOrgUnit.nodeId)
    } catch (e) {
      console.error(e)
    } finally {
      this.saving = false
    }
  }

  async onRemoveOrgUnit(orgUnitId: string): Promise<void> {
    const parentOrgUnitId = this.orgChartNodes.find(node => node.id === orgUnitId).pid
    const params = {
      parentOrgUnitId,
      baseOrgUnitId: orgUnitId
    } as RemoveOrgUnitModel
    try {
      this.$emit('update:loading', true)
      const orgUnitIdsToRemove = await this[ACTION_DELETE_ORG_UNIT](params)
      this.collapseNodes(orgUnitIdsToRemove)
    } catch (e) {
      console.error(e)
    } finally {
      this.$emit('update:loading', false)
    }
  }
}
