



























































































import { RouteName } from '@/router'
import { namespace } from 'vuex-class'
import { Component, Vue, Watch } from 'vue-property-decorator'
import {
  ACTION_ADD_NEW_CHILD,
  ACTION_CLEAR_EXPANDED_IDS,
  ACTION_CLEAR_ORG_UNIT_STATE,
  ACTION_FETCH_ORG,
  ACTION_FETCH_ORG_UNIT,
  ACTION_FETCH_ORG_UNITS,
  ACTION_REMOVE_SELECTED_ORG
} from '@/store/action-types'
import {
  AddOrgUnitModel,
  AssignWorkgroupsModel,
  GetOrgUnitParams,
  MoveOrgUnitTreeModel,
  MoveWorkgroupsModel,
  OrganizationViewModel,
  UnassignWorkgroupsModel,
  ViewTreeNodeModel,
  WorkgroupViewModel
} from '@/models/hierarchy.models'
import { Dictionary } from 'vue-router/types/router'
import LoadingSpinner from '@/components/shared/LoadingSpinner.vue'
import OrgUnitToolbar from '@/components/org-unit/OrgUnitToolbar.vue'
import { OrganizationStatus, OrgUnitDataTypes } from '@/shared/constants/enum'
import WorkgroupsDrawer from '@/components/org-unit/WorkgroupsDrawer.vue'
import { ConfirmDialog, NameDialog } from '@/models/dialog.models'
import { OrgUnitsDrawerModel } from '@/models/drawer.models'
import ConfirmDialogComponent from '@/components/shared/ConfirmDialogComponent.vue'
import NameDialogComponent from '@/components/shared/NameDialogComponent.vue'
import OrgUnitsDrawer from '@/components/org-unit/OrgUnitsDrawer.vue'
import { $http } from '@/plugins/http'

const orgUnit = namespace('OrgUnit')
const orgUnits = namespace('OrgUnits')
const organization = namespace('Organization')

@Component({
  components: {
    OrgUnitsDrawer,
    ConfirmDialogComponent,
    WorkgroupsDrawer,
    LoadingSpinner,
    OrgUnitToolbar,
    NameDialogComponent
  }
})
export default class OrgUnitDetail extends Vue {
  selectedTab = null
  addOrgUnitDialog = null
  saving = false
  loading = false
  workgroupsDrawer = false

  unassignedWorkgroups: WorkgroupViewModel[] = []
  confirmDialog: ConfirmDialog = null
  orgUnitsDrawer: OrgUnitsDrawerModel = null

  @organization.State
  selectedOrg: OrganizationViewModel

  @organization.Getter
  leafLabel: string

  @organization.Getter
  selectedOrgStatus: number

  @orgUnit.State
  selectedOrgUnit: ViewTreeNodeModel

  @orgUnit.State
  selectedWorkgroups: WorkgroupViewModel[]

  @orgUnit.State
  selectedChildren: ViewTreeNodeModel[]

  @orgUnit.Getter
  selectedChildrenWorkgroupsCount: number

  @organization.Action
  [ACTION_FETCH_ORG]: (hierarchyId: string) => Promise<OrganizationViewModel>

  @organization.Action
  [ACTION_REMOVE_SELECTED_ORG]: () => void

  @orgUnit.Action
  [ACTION_FETCH_ORG_UNIT]: (params: GetOrgUnitParams) => Promise<ViewTreeNodeModel>

  @orgUnit.Action
  [ACTION_CLEAR_ORG_UNIT_STATE]: () => void

  @orgUnits.State
  orgUnits: ViewTreeNodeModel[]

  @orgUnits.Action
  [ACTION_CLEAR_EXPANDED_IDS]: () => void

  @orgUnits.Action
  [ACTION_FETCH_ORG_UNITS]: () => Promise<ViewTreeNodeModel[]>

  @orgUnit.Action
  [ACTION_ADD_NEW_CHILD]: (params: AddOrgUnitModel) => Promise<ViewTreeNodeModel>

  get query(): Dictionary<string | (string | null)[]> {
    return this.$route.query
  }

  get hierarchyId(): string {
    return this.query.hierarchyId as string
  }

  get orgUnitId(): string {
    return this.query.orgUnitId as string
  }

  get childrenCount(): string {
    return (this.selectedOrgUnit && this.selectedOrgUnit.childCount) ? this.selectedOrgUnit.childCount.toString() : '0'
  }

  get workgroupsCount(): string {
    return (this.selectedOrgUnit && this.selectedOrgUnit.workgroupsCount) ? this.selectedOrgUnit.workgroupsCount.toString() : '0'
  }

  get tabs(): any {
    return [
      {
        label: 'Children',
        to: { name: RouteName.children, query: this.query },
        count: this.childrenCount
      },
      {
        label: this.leafLabel || 'Workgroups',
        to: { name: RouteName.workgroups, query: this.query },
        count: this.workgroupsCount
      }
    ]
  }

  get orgUnitIsEditable(): boolean {
    return this.selectedOrgStatus !== OrganizationStatus.Active
  }

  get activeOrgUnitDataType(): number {
    return this.$route.meta.type
  }

  get activeTabHasSelectedItems(): boolean {
    if (this.activeOrgUnitDataType === OrgUnitDataTypes.Children) {
      return this.selectedChildren.length > 0
    } else if (this.activeOrgUnitDataType === OrgUnitDataTypes.Workgroups) {
      return this.selectedWorkgroups.length > 0
    } else {
      return false
    }
  }

  @Watch('hierarchyId', { immediate: true })
  async onHierarchyIdChange(): Promise<void> {
    this[ACTION_REMOVE_SELECTED_ORG]()
    await this.fetchOrg()
    if (!this.selectedOrg) {
      this[ACTION_CLEAR_EXPANDED_IDS]()
      this.closeClick()
    }
    await this.onOrgUnitIdChange()
  }

  @Watch('orgUnitId')
  async onOrgUnitIdChange(): Promise<void> {
    this[ACTION_CLEAR_ORG_UNIT_STATE]()
    await this.fetchOrgUnit()
    if (!this.selectedOrgUnit) {
      this.closeClick()
    }
  }

  destroyed(): void {
    this[ACTION_CLEAR_ORG_UNIT_STATE]()
  }

  async fetchOrg(): Promise<OrganizationViewModel> {
    return this[ACTION_FETCH_ORG](this.hierarchyId)
  }

  async fetchOrgUnit(): Promise<ViewTreeNodeModel> {
    const params = {
      hierarchyRoot: this.hierarchyId,
      orgUnitId: this.orgUnitId,
      setSelected: true
    } as GetOrgUnitParams
    return this[ACTION_FETCH_ORG_UNIT](params)
  }

  closeClick(): void {
    const { hierarchyId } = this
    this.$router.push({ name: RouteName.home, query: { hierarchyId } })
  }

  showAddOrgUnitDialog(): void {
    const params = {
      title: 'Add New Org Unit',
      placeHolderText: 'New Org Unit Name',
      helperText: `will be added to <strong>${this.selectedOrgUnit.nodeName}</strong>`,
      buttonText: 'Add Org Unit'
    } as NameDialog
    this.addOrgUnitDialog = new NameDialog(params)
  }

  async addOrgUnit(orgUnitName: string): Promise<void> {
    const params = {
      orgUnitName,
      parentOrgUnitId: this.selectedOrgUnit.nodeId
    } as AddOrgUnitModel
    try {
      this.saving = true
      await this[ACTION_ADD_NEW_CHILD](params)
      await this.onOrgUnitIdChange()
      this.addOrgUnitDialog = null
    } catch (e) {
      console.error(e)
    } finally {
      this.saving = false
    }
  }

  openWorkgroupsDrawer(): void {
    this.workgroupsDrawer = true
    this.fetchUnassignedWorkgroups()
  }

  async fetchUnassignedWorkgroups(): Promise<void> {
    try {
      this.loading = true
      const { data } = await this.$http.hierarchy.get(
        '/workgroups/getunassignedworkgroups',
        {
          params: {
            hierarchyId: this.hierarchyId
          }
        }
      )
      this.unassignedWorkgroups = data
    } catch (e) {
      console.error(e)
    } finally {
      this.loading = false
    }
  }

  async assignWorkgroups(params: AssignWorkgroupsModel): Promise<void> {
    try {
      this.saving = true
      await this.$http.hierarchy.put(
        '/workgroups/assignworkgroupstoorgunit',
        params
      )
      this.workgroupsDrawer = false
      await this.onOrgUnitIdChange()
    } catch (e) {
      console.error(e)
    } finally {
      this.saving = false
    }
  }

  async moveWorkgroups(params: AssignWorkgroupsModel): Promise<void> {
    try {
      this.saving = true
      await this.$http.hierarchy.put(
        '/workgroups/moveworkgroupstoorgunit',
        params
      )
      this.orgUnitsDrawer = null
      await this.onOrgUnitIdChange()
    } catch (e) {
      console.error(e)
    } finally {
      this.saving = false
    }
  }

  async moveChildrenWorkgroups(params: MoveWorkgroupsModel): Promise<void> {
    try {
      this.saving = true
      await this.$http.hierarchy.post(
        '/workgroups/moveworkgroupstoneworgunit',
        params
      )
      this.orgUnitsDrawer = null
      await this.onOrgUnitIdChange()
    } catch (e) {
      console.error(e)
    } finally {
      this.saving = false
    }
  }

  assignSelectedWorkgroups(workgroupIds: string[]): void {
    const params = {
      workgroupIds,
      hierarchyId: this.hierarchyId,
      newOrgUnitId: this.orgUnitId
    } as AssignWorkgroupsModel
    this.assignWorkgroups(params)
  }

  unAssignWorkgroupsDialog(): void {
    const title = `Un-Assign ${this.leafLabel}`
    const params = {
      title,
      text: `Are you sure you want to remove these <strong>${this.selectedWorkgroups.length} ${this.leafLabel}</strong>
            from <strong>${this.selectedOrgUnit.nodeName}</strong> and mark them un-assigned?`,
      buttonText: title
    } as ConfirmDialog
    this.confirmDialog = new ConfirmDialog(params)
  }

  async unAssignWorkgroups(): Promise<void> {
    const workgroupIds = this.selectedWorkgroups.map(workgroup => workgroup.workgroupId)
    const params = {
      workgroupIds,
      hierarchyId: this.hierarchyId,
      orgUnitId: this.selectedOrgUnit.nodeId
    } as UnassignWorkgroupsModel
    try {
      this.saving = true
      await this.$http.hierarchy.put(
        '/workgroups/unassignworkgroupsfromorgunit',
        params
      )
      this.confirmDialog = null
      await this.onOrgUnitIdChange()
    } catch (e) {
      console.error(e)
    } finally {
      this.saving = false
    }
  }

  async fetchOrgUnits(): Promise<void> {
    try {
      this.loading = true
      await this[ACTION_FETCH_ORG_UNITS]()
    } catch (e) {
      console.error(e)
    } finally {
      this.loading = false
    }
  }

  async moveWorkgroupsDrawer(): Promise<void> {
    const text = `Move <strong>${this.selectedWorkgroups.length} ${this.leafLabel}</strong> to: `
    const buttonText = `Move ${this.leafLabel}`
    const dataType = OrgUnitDataTypes.Workgroups
    const assignmentType = OrgUnitDataTypes.Workgroups
    this.orgUnitsDrawer = new OrgUnitsDrawerModel(text, dataType, buttonText, assignmentType)
    await this.fetchOrgUnits()
  }

  async moveChildrenDrawer(): Promise<void> {
    const text = `Move <strong>${this.selectedChildren.length} Org Units</strong> to: `
    const buttonText = 'Move Children'
    const dataType = OrgUnitDataTypes.Children
    this.orgUnitsDrawer = new OrgUnitsDrawerModel(text, dataType, buttonText)
    await this.fetchOrgUnits()
  }

  async moveChildrenWorkgroupsDrawer(): Promise<void> {
    const text = `Move <strong>${this.selectedChildrenWorkgroupsCount} ${this.leafLabel}</strong> to: `
    const buttonText = `Move ${this.leafLabel}`
    const dataType = OrgUnitDataTypes.Workgroups
    const assignmentType = OrgUnitDataTypes.Children
    this.orgUnitsDrawer = new OrgUnitsDrawerModel(text, dataType, buttonText, assignmentType)
    await this.fetchOrgUnits()
  }

  assignToSelectedOrgUnit(orgUnitId: string): void {
    if (this.orgUnitsDrawer.dataType === OrgUnitDataTypes.Workgroups) {
      // Moving WORKGROUPS from one org unit to another
      if (this.orgUnitsDrawer.assignmentType === OrgUnitDataTypes.Children) {
        // Moving WORKGROUPS of all selected children to another org unit
        const params = {
          hierarchyId: this.hierarchyId,
          newOrgUnitId: orgUnitId,
          orgUnits: this.selectedChildren.map(orgUnit => orgUnit.nodeId)
        } as MoveWorkgroupsModel
        this.moveChildrenWorkgroups(params)
      } else if (this.orgUnitsDrawer.assignmentType === OrgUnitDataTypes.Workgroups) {
        // Moving selected WORKGROUPS to another org unit
        const params = {
          workgroupIds: this.selectedWorkgroups.map(workgroup => workgroup.workgroupId),
          hierarchyId: this.hierarchyId,
          newOrgUnitId: orgUnitId
        } as AssignWorkgroupsModel
        this.moveWorkgroups(params)
      }
    } else if (this.orgUnitsDrawer.dataType === OrgUnitDataTypes.Children) {
      // Moving CHILD org units to another org unit
      const params = {
        hierarchyId: this.hierarchyId,
        oldParentNodeId: this.orgUnitId,
        newParentNodeId: orgUnitId,
        orgUnits: this.selectedChildren.map(orgUnit => orgUnit.nodeId)
      } as MoveOrgUnitTreeModel
      this.moveChildren(params)
    }
  }

  async moveChildren(params: MoveOrgUnitTreeModel): Promise<void> {
    try {
      this.saving = true
      await $http.hierarchy.post('/orgunits/moveorgunittree', params)
      this.orgUnitsDrawer = null
      await this.onOrgUnitIdChange()
    } catch (e) {
      console.error(e)
    } finally {
      this.saving = false
    }
  }
}
