import { Action, Module, Mutation, VuexModule } from 'vuex-module-decorators'
import store from '@/store'
import { AddOrgUnitModel, GetOrgUnitParams, ViewTreeNodeModel, WorkgroupViewModel } from '@/models/hierarchy.models'
import {
  ACTION_ADD_NEW_CHILD,
  ACTION_CLEAR_ORG_UNIT_STATE,
  ACTION_CLEAR_SELECTED_CHILDREN,
  ACTION_CLEAR_SELECTED_WORKGROUPS,
  ACTION_FETCH_CHILDREN_FOR_ORG_UNIT,
  ACTION_FETCH_ORG_UNIT,
  ACTION_FETCH_WORKGROUPS_FOR_ORG_UNIT,
  ACTION_SET_SELECTED_CHILDREN,
  ACTION_SET_SELECTED_WORKGROUPS
} from '@/store/action-types'
import { $http } from '@/plugins/http'
import {
  MUTATION_SET_CHILDREN, MUTATION_SET_SELECTED_CHILDREN,
  MUTATION_SET_SELECTED_ORG_UNIT,
  MUTATION_SET_SELECTED_WORKGROUPS,
  MUTATION_SET_WORKGROUPS
} from '@/store/mutation-types'

@Module({
  namespaced: true,
  store,
  preserveState: true,
  name: 'OrgUnit'
})
class OrgUnit extends VuexModule {
  selectedOrgUnit: ViewTreeNodeModel = null
  children: ViewTreeNodeModel[] = []
  workgroups: WorkgroupViewModel[] = []
  selectedChildren: ViewTreeNodeModel[] = []
  selectedWorkgroups: WorkgroupViewModel[] = []

  get selectedOrgUnitId(): string {
    return this.selectedOrgUnit && this.selectedOrgUnit.nodeId
  }

  get selectedChildrenWorkgroupsCount(): number {
    let count = 0
    this.selectedChildren.forEach((child) => {
      count += child.workgroupsCount
    })
    return count
  }

  @Mutation
  [MUTATION_SET_SELECTED_ORG_UNIT](orgUnit: ViewTreeNodeModel): void {
    this.selectedOrgUnit = orgUnit
  }

  @Mutation
  [MUTATION_SET_WORKGROUPS](workgroups: WorkgroupViewModel[]): void {
    this.workgroups = workgroups
  }

  @Mutation
  [MUTATION_SET_SELECTED_WORKGROUPS](workgroups: WorkgroupViewModel[]): void {
    this.selectedWorkgroups = workgroups
  }

  @Mutation
  [MUTATION_SET_CHILDREN](children: ViewTreeNodeModel[]): void {
    this.children = children
  }

  @Mutation
  [MUTATION_SET_SELECTED_CHILDREN](children: ViewTreeNodeModel[]): void {
    this.selectedChildren = children
  }

  @Action
  async [ACTION_FETCH_ORG_UNIT](params: GetOrgUnitParams): Promise<ViewTreeNodeModel> {
    const { data }: { data: ViewTreeNodeModel } = await $http.hierarchy.get(
      '/orgunits/loadorgunitinfo',
      { params }
    )
    if (data && params.setSelected) {
      this.context.commit(MUTATION_SET_SELECTED_ORG_UNIT, data)
    }
    return data
  }

  @Action
  async [ACTION_FETCH_WORKGROUPS_FOR_ORG_UNIT](orgUnitId: string): Promise<WorkgroupViewModel[]> {
    const { data }: { data: WorkgroupViewModel[] } = await $http.hierarchy.get(
      '/workgroups/loadWorkgroupsInOrgUnit',
      {
        params: {
          orgUnitId
        }
      }
    )
    if (data && Array.isArray(data) && data.length > 0) {
      this.context.commit(MUTATION_SET_WORKGROUPS, data)
    } else {
      this.context.commit(MUTATION_SET_WORKGROUPS, [])
    }
    return data
  }

  @Action
  [ACTION_SET_SELECTED_WORKGROUPS](workgroups: WorkgroupViewModel[]): void {
    this.context.commit(MUTATION_SET_SELECTED_WORKGROUPS, workgroups)
  }

  @Action
  [ACTION_CLEAR_SELECTED_WORKGROUPS](): void {
    this.context.commit(MUTATION_SET_SELECTED_WORKGROUPS, [])
  }

  @Action
  async [ACTION_FETCH_CHILDREN_FOR_ORG_UNIT](params: GetOrgUnitParams): Promise<ViewTreeNodeModel[]> {
    const { data }: { data: ViewTreeNodeModel[] } = await $http.hierarchy.get(
      '/orgunits/loadorgunitssection',
      {
        params
      }
    )
    if (data && Array.isArray(data) && data.length > 0) {
      const immediateChildren = data.filter(child => child.parentNodeId === params.orgUnitId)
      this.context.commit(MUTATION_SET_CHILDREN, immediateChildren)
    } else {
      this.context.commit(MUTATION_SET_CHILDREN, [])
    }
    return data
  }

  @Action
  async [ACTION_ADD_NEW_CHILD](params: AddOrgUnitModel): Promise<ViewTreeNodeModel> {
    const { data }: { data: ViewTreeNodeModel } = await $http.hierarchy.post(
      '/orgunits/addorgunit',
      params
    )
    return data
  }

  @Action
  [ACTION_SET_SELECTED_CHILDREN](children: ViewTreeNodeModel[]): void {
    this.context.commit(MUTATION_SET_SELECTED_CHILDREN, children)
  }

  @Action
  [ACTION_CLEAR_SELECTED_CHILDREN](): void {
    this.context.commit(MUTATION_SET_SELECTED_CHILDREN, [])
  }

  @Action
  [ACTION_CLEAR_ORG_UNIT_STATE](): void {
    this.context.commit(MUTATION_SET_SELECTED_ORG_UNIT, null)
    this.context.commit(MUTATION_SET_WORKGROUPS, [])
    this.context.commit(MUTATION_SET_SELECTED_WORKGROUPS, [])
    this.context.commit(MUTATION_SET_CHILDREN, [])
    this.context.commit(MUTATION_SET_SELECTED_CHILDREN, [])
  }
}

export default OrgUnit
