import { Action, Module, Mutation, VuexModule } from 'vuex-module-decorators'
import store, { organization, orgUnit } from '@/store'
import {
  ACTION_ADD_ORG_UNIT,
  ACTION_DELETE_ORG_UNIT,
  ACTION_FETCH_ORG_UNITS,
  ACTION_FETCH_ORG_UNIT,
  ACTION_MOVE_ORG_UNITS,
  ACTION_ADD_EXPANDED_IDS,
  ACTION_REMOVE_EXPANDED_IDS,
  ACTION_CLEAR_EXPANDED_IDS,
  ACTION_CLEAR_ORG_UNITS_STATE,
  ACTION_ADD_NEW_CHILD
} from '@/store/action-types'
import {
  MUTATION_REMOVE_ORG_UNIT_BY_ID,
  MUTATION_SET_ORG_UNITS,
  MUTATION_UPDATE_ORG_UNIT,
  MUTATION_ADD_ORG_UNITS,
  MUTATION_ADD_EXPANDED_IDS,
  MUTATION_REMOVE_EXPANDED_IDS,
  MUTATION_CLEAR_EXPANDED_IDS
} from '@/store/mutation-types'
import { $http } from '@/plugins/http'
import {
  ViewTreeNodeModel,
  OrgChartNode,
  MoveOrgUnitTreeModel,
  AddOrgUnitModel,
  RemoveOrgUnitModel,
  ReplaceNodeAndChildrenParams, GetOrgUnitParams
} from '@/models/hierarchy.models'

@Module({
  namespaced: true,
  store,
  preserveState: true,
  name: 'OrgUnits'
})
class OrgUnits extends VuexModule {
  orgUnits: ViewTreeNodeModel[] = []
  expandedIds: string[] = []

  get orgChartNodes(): OrgChartNode[] {
    const leafLabel = organization.leafLabel || ''
    if (!this.orgUnits) {
      return []
    } else {
      return this.orgUnits.map((item) => {
        return {
          id: item.nodeId,
          pid: item.parentNodeId,
          name: item.nodeName,
          childCount: item.childCount,
          workgroupsCount: `${item.workgroupsCount} ${leafLabel}`
        }
      })
    }
  }

  @Mutation
  [MUTATION_SET_ORG_UNITS](org: ViewTreeNodeModel[]): void {
    this.orgUnits = org
  }

  @Mutation
  [MUTATION_REMOVE_ORG_UNIT_BY_ID](id: string): void {
    const foundIndex = this.orgUnits.findIndex(item => item.nodeId === id)
    if (foundIndex > -1) {
      this.orgUnits.splice(foundIndex, 1)
    }
  }

  @Mutation
  [MUTATION_UPDATE_ORG_UNIT](params: ReplaceNodeAndChildrenParams): void {
    const { nodeId } = params
    const foundIndex = this.orgUnits.findIndex(item => item.nodeId === nodeId)
    if (foundIndex > -1) {
      this.orgUnits.splice(foundIndex, 1, ...params.nodes)
    }
  }

  @Mutation
  [MUTATION_ADD_ORG_UNITS](orgUnits: ViewTreeNodeModel[]): void {
    orgUnits.forEach((orgUnit) => {
      this.orgUnits.push(orgUnit)
    })
  }

  @Mutation
  [MUTATION_ADD_EXPANDED_IDS](ids: string[]): void {
    ids.forEach((id) => {
      if (!this.expandedIds.includes(id)) {
        this.expandedIds.push(id)
      }
    })
  }

  @Mutation
  [MUTATION_REMOVE_EXPANDED_IDS](ids: string[]): void {
    ids.forEach((id) => {
      const foundIndex = this.expandedIds.indexOf(id)
      if (foundIndex > -1) {
        this.expandedIds.splice(foundIndex, 1)
      }
    })
  }

  @Mutation
  [MUTATION_CLEAR_EXPANDED_IDS](): void {
    this.expandedIds = []
  }

  @Action
  async [ACTION_FETCH_ORG_UNITS](): Promise<ViewTreeNodeModel[]> {
    const { data } = await $http.hierarchy.get('/orgunits/loadorgunitstree', {
      params: {
        hierarchyRoot: organization.selectedHierarchyId
      }
    })
    this.context.commit(MUTATION_SET_ORG_UNITS, data)
    return data
  }

  @Action
  async [ACTION_MOVE_ORG_UNITS](params: MoveOrgUnitTreeModel): Promise<ViewTreeNodeModel[]> {
    const { data }: { data: ViewTreeNodeModel[] } = await $http.hierarchy.post(
      '/orgunits/moveorgunittree',
      params
    )
    // Remove moved OrgUnits and their children
    params.orgUnits.forEach((orgUnitId) => {
      const foundNodes = this.orgUnits.filter(item => {
        return item.nodeId === orgUnitId || item.parentNodeId.startsWith(orgUnitId)
      })
      foundNodes.forEach((node) => {
        this.context.commit(MUTATION_REMOVE_ORG_UNIT_BY_ID, node.nodeId)
      })
    })
    // Remove children of updated OrgUnit
    const foundNodes = this.orgUnits.filter(item => {
      return item.parentNodeId.startsWith(params.newParentNodeId)
    })
    foundNodes.forEach((node) => {
      this.context.commit(MUTATION_REMOVE_ORG_UNIT_BY_ID, node.nodeId)
    })
    // Update parent OrgUnit section with data returned from API
    const replaceNodeParams = {
      nodeId: params.newParentNodeId,
      nodes: data
    } as ReplaceNodeAndChildrenParams
    this.context.commit(MUTATION_UPDATE_ORG_UNIT, replaceNodeParams)
    // Fetch updated OrgUnit where nodes were moved from
    const getOrgUnitParams = {
      hierarchyRoot: organization.selectedHierarchyId,
      orgUnitId: params.oldParentNodeId
    } as GetOrgUnitParams
    const parentOfMovedSection = await orgUnit[ACTION_FETCH_ORG_UNIT](getOrgUnitParams)
    const parentOfMovedSectionParams = {
      nodeId: params.oldParentNodeId,
      nodes: [parentOfMovedSection]
    } as ReplaceNodeAndChildrenParams
    this.context.commit(MUTATION_UPDATE_ORG_UNIT, parentOfMovedSectionParams)
    return data
  }

  @Action
  async [ACTION_ADD_ORG_UNIT](params: AddOrgUnitModel): Promise<ViewTreeNodeModel> {
    const newOrgUnit = await orgUnit[ACTION_ADD_NEW_CHILD](params)
    const getOrgUnitParams = {
      hierarchyRoot: organization.selectedHierarchyId,
      orgUnitId: params.parentOrgUnitId
    } as GetOrgUnitParams
    const updatedParent = await orgUnit[ACTION_FETCH_ORG_UNIT](getOrgUnitParams)
    this.context.commit(MUTATION_ADD_ORG_UNITS, [newOrgUnit])
    const updatedParentParams = {
      nodeId: params.parentOrgUnitId,
      nodes: [updatedParent]
    } as ReplaceNodeAndChildrenParams
    this.context.commit(MUTATION_UPDATE_ORG_UNIT, updatedParentParams)
    return newOrgUnit
  }

  @Action
  async [ACTION_DELETE_ORG_UNIT](params: RemoveOrgUnitModel): Promise<string[]> {
    // TODO: this always returns 200, true no matter what.  Need to fix that
    const { data }: { data: boolean } = await $http.hierarchy.post(
      '/orgunits/setorgunittreeasexpired',
      null,
      {
        params: {
          baseOrgUnitId: params.baseOrgUnitId
        }
      }
    )
    if (!data) {
      return
    }
    let orgUnitIdsToRemove = [params.baseOrgUnitId]
    const childOrgUnitIds = this.orgUnits.filter(orgUnit => orgUnit.parentNodeId.startsWith(params.baseOrgUnitId)).map(orgUnit => orgUnit.nodeId)
    orgUnitIdsToRemove = orgUnitIdsToRemove.concat(childOrgUnitIds)
    orgUnitIdsToRemove.forEach((id) => {
      this.context.commit(MUTATION_REMOVE_ORG_UNIT_BY_ID, id)
    })
    const getOrgUnitParams = {
      hierarchyRoot: organization.selectedHierarchyId,
      orgUnitId: params.parentOrgUnitId
    } as GetOrgUnitParams
    const updatedParent = await orgUnit[ACTION_FETCH_ORG_UNIT](getOrgUnitParams)
    const updatedParentParams = {
      nodeId: params.parentOrgUnitId,
      nodes: [updatedParent]
    } as ReplaceNodeAndChildrenParams
    this.context.commit(MUTATION_UPDATE_ORG_UNIT, updatedParentParams)
    return orgUnitIdsToRemove
  }

  @Action
  [ACTION_ADD_EXPANDED_IDS](ids: string[]): void {
    if (ids && ids.length) {
      this.context.commit(MUTATION_ADD_EXPANDED_IDS, ids)
    }
  }

  @Action
  [ACTION_REMOVE_EXPANDED_IDS](ids: string[]): void {
    if (ids && ids.length) {
      this.context.commit(MUTATION_REMOVE_EXPANDED_IDS, ids)
    }
  }

  @Action
  [ACTION_CLEAR_EXPANDED_IDS](): void {
    this.context.commit(MUTATION_CLEAR_EXPANDED_IDS)
  }

  @Action
  [ACTION_CLEAR_ORG_UNITS_STATE](): void {
    this.context.commit(MUTATION_SET_ORG_UNITS, [])
    this.context.commit(MUTATION_CLEAR_EXPANDED_IDS)
  }
}

export default OrgUnits
