






































































































import { Component, Vue, Prop } from 'vue-property-decorator'
import { WorkgroupViewModel } from '@/models/hierarchy.models'
import LoadingSpinner from '@/components/shared/LoadingSpinner.vue'

@Component({
  components: { LoadingSpinner }
})
export default class WorkgroupsDrawer extends Vue {
  @Prop() open: boolean
  @Prop() saving: boolean
  @Prop() loading: boolean
  @Prop() leafLabel: string
  @Prop() orgUnitName: string
  @Prop({ default: () => [] }) workgroups: WorkgroupViewModel[]

  selectedWorkgroupIds: string[] = []
  search = ''

  get filteredWorkgroups(): WorkgroupViewModel[] {
    if (!this.search) {
      return this.workgroups
    } else {
      return this.workgroups.filter(workgroup => workgroup.workgroupName.toLowerCase().indexOf(this.search.toLocaleLowerCase()) > -1)
    }
  }

  closeClick(): void {
    this.onInput(false)
  }

  onInput(open: boolean): void {
    this.$emit('update:open', open)
    if (!open) {
      this.selectedWorkgroupIds = []
      this.search = ''
    }
  }

  selectWorkgroups(): void {
    this.$emit('selectWorkgroups', this.selectedWorkgroupIds)
  }
}
