

















import { Component, Prop, Vue, Watch } from 'vue-property-decorator'
import { GetOrgUnitParams, ViewTreeNodeModel } from '@/models/hierarchy.models'
import { namespace } from 'vuex-class'
import {
  ACTION_CLEAR_SELECTED_CHILDREN,
  ACTION_FETCH_CHILDREN_FOR_ORG_UNIT,
  ACTION_SET_SELECTED_CHILDREN
} from '@/store/action-types'

const organization = namespace('Organization')
const orgUnit = namespace('OrgUnit')

@Component({})
export default class ChildrenTab extends Vue {
  @Prop() editable: boolean

  loading = false

  selectedChildren: ViewTreeNodeModel[] = []

  @organization.Getter
  selectedHierarchyId: string

  @organization.Getter
  leafLabel: string

  @orgUnit.Getter
  selectedOrgUnitId: string

  @orgUnit.State
  children: ViewTreeNodeModel[]

  @orgUnit.Action
  [ACTION_FETCH_CHILDREN_FOR_ORG_UNIT]: (params: GetOrgUnitParams) => ViewTreeNodeModel[]

  @orgUnit.Action
  [ACTION_SET_SELECTED_CHILDREN]: (selectedChildren: ViewTreeNodeModel[]) => void

  @orgUnit.Action
  [ACTION_CLEAR_SELECTED_CHILDREN]: () => void

  get headers(): any[] {
    return [
      { text: 'Name', value: 'nodeName' },
      { text: this.leafLabel || 'Workgroups', value: 'workgroupsCount' },
      { text: 'Children', value: 'childCount' }
    ]
  }

  get params(): GetOrgUnitParams {
    return {
      hierarchyRoot: this.selectedHierarchyId,
      orgUnitId: this.selectedOrgUnitId
    }
  }

  @Watch('params', { immediate: true, deep: true })
  onParamsChange(): void {
    this.fetchChildren()
  }

  destroyed(): void {
    this[ACTION_CLEAR_SELECTED_CHILDREN]()
  }

  async fetchChildren(): Promise<void> {
    try {
      this.loading = true
      await this[ACTION_FETCH_CHILDREN_FOR_ORG_UNIT](this.params)
    } catch (e) {
      console.error(e)
    } finally {
      this.loading = false
    }
  }

  setSelectedChildren(selectedChildren: ViewTreeNodeModel[]): void {
    this[ACTION_SET_SELECTED_CHILDREN](selectedChildren)
  }
}
