import { OrgUnitDataTypes } from '@/shared/constants/enum'

export class OrgUnitsDrawerModel {
  text: string
  dataType: OrgUnitDataTypes
  buttonText?: string
  assignmentType?: OrgUnitDataTypes
  constructor(text: string, dataType: OrgUnitDataTypes, buttonText?: string, assignmentType?: OrgUnitDataTypes) {
    this.text = text
    this.dataType = dataType
    this.buttonText = buttonText || 'Submit'
    this.assignmentType = assignmentType
  }
}
