import { Module, Action, Mutation, VuexModule } from 'vuex-module-decorators'
import store from '@/store'
import { OrganizationViewModel } from '@/models/hierarchy.models'
import { MUTATION_SET_ORGS, MUTATION_SET_SELECTED_ORG } from '@/store/mutation-types'
import {
  ACTION_REMOVE_SELECTED_ORG,
  ACTION_FETCH_ORG,
  ACTION_FETCH_ORGS,
  ACTION_SET_SELECTED_ORG_BY_HIERARCHY_CODE
} from '@/store/action-types'
import { $http } from '@/plugins/http'

@Module({
  namespaced: true,
  store,
  preserveState: true,
  name: 'Organization'
})
class Organization extends VuexModule {
  orgs: OrganizationViewModel[] = []
  selectedOrg: OrganizationViewModel = null

  get selectedHierarchyId(): string {
    return this.selectedOrg && this.selectedOrg.hierarchyId
  }

  get leafLabel(): string {
    return this.selectedOrg && this.selectedOrg.leafLabel
  }

  get selectedOrgStatus(): number {
    return this.selectedOrg && this.selectedOrg.organizationStatus
  }

  @Mutation
  [MUTATION_SET_ORGS](orgs: OrganizationViewModel[]): void {
    this.orgs = orgs
  }

  @Mutation
  [MUTATION_SET_SELECTED_ORG](org: OrganizationViewModel): void {
    this.selectedOrg = org
  }

  @Action
  async [ACTION_FETCH_ORG](hierarchyId: string): Promise<OrganizationViewModel> {
    const { data } = await $http.hierarchy.get('/getorganization', {
      params: {
        hierarchyId
      }
    })
    if (data) {
      this.context.commit(MUTATION_SET_SELECTED_ORG, data)
    }
    return data
  }

  @Action
  async [ACTION_FETCH_ORGS](): Promise<OrganizationViewModel[]> {
    const { data } = await $http.hierarchy.get('/getorgs', {
      params: {
        applicationId: process.env.VUE_APP_APPLICATION_ID
      }
    })
    this.context.commit(MUTATION_SET_ORGS, data)
    return data
  }

  @Action
  [ACTION_SET_SELECTED_ORG_BY_HIERARCHY_CODE](hierarchyId: string): void {
    if (!this.orgs || !this.orgs.length) {
      return
    }
    const foundOrg = this.orgs.find(org => org.hierarchyId === hierarchyId)
    if (foundOrg) {
      this.context.commit(MUTATION_SET_SELECTED_ORG, foundOrg)
    } else {
      this.context.commit(MUTATION_SET_SELECTED_ORG, null)
    }
  }

  @Action
  [ACTION_REMOVE_SELECTED_ORG](): void {
    this.context.commit(MUTATION_SET_SELECTED_ORG, null)
  }
}

export default Organization
