import Vue from 'vue'
import Vuex from 'vuex'
import OrgUnits from '@/store/modules/orgUnits'
import Organization from '@/store/modules/organization'
import OrgUnit from '@/store/modules/orgUnit'
import { getModule } from 'vuex-module-decorators'
import { orgUnitsPersist } from '@/store/vuex-persist'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
  },
  mutations: {
  },
  actions: {
  },
  modules: {
    Organization,
    OrgUnits,
    OrgUnit
  },
  plugins: [
    orgUnitsPersist.plugin
  ]
})

export default store

export const organization = getModule(Organization, store)
export const orgUnits = getModule(OrgUnits, store)
export const orgUnit = getModule(OrgUnit, store)
