export class ConfirmDialog {
  title: string
  text: string
  buttonText?: string
  buttonColor?: string
  constructor(params: ConfirmDialog) {
    this.title = params.title
    this.text = params.text
    this.buttonText = params.buttonText || 'Confirm'
    this.buttonColor = params.buttonColor || 'primary'
  }
}

export class NameDialog {
  editing: boolean
  title: string
  placeHolderText: string
  buttonText: string
  buttonColor?: string
  helperText?: string
  name?: string
  constructor(params: NameDialog) {
    this.editing = !!params.editing
    this.title = params.title
    this.placeHolderText = params.placeHolderText
    this.buttonText = params.buttonText
    this.buttonColor = params.buttonColor || 'primary'
    this.helperText = params.helperText
    this.name = params.name
  }
}
