import OrgChart from '@/lib/orgchart.js'
import { RouteName } from '@/router'

const DEFAULT_TEMPLATE = 'DEFAULT_TEMPLATE'
const SVG_SELECTOR = 'OrgChartSVG'

function OrgChartTemplateFactory(): void {
  const { templates } = OrgChart
  const template = templates[DEFAULT_TEMPLATE] = Object.assign({}, OrgChart.templates.ana)
  template.svg = `<svg
                    xmlns="http://www.w3.org/2000/svg"
                    xmlns:xlink="http://www.w3.org/1999/xlink"
                    id=${SVG_SELECTOR}
                    style="display:block;"
                    width="{w}"
                    height="{h}"
                    viewBox="{viewBox}"
                   >
                    {content}
                   </svg>`
  template.size = [270, 170]
  template.node = '<rect  class="outer-rect" x="0" y="0" width="270" height="170"></rect>'
  template.node += '<rect class="inner-rect" x="10" y="10" width="250" height="150"></rect>'
  template.name = '<text class="name-text" text-anchor="middle" x="135" y="93">{val}</text>'
  template.childCount = `<text data-route="${RouteName.children}" class="count-text" text-anchor="start" x="20" y="150">{val} Children</text>`
  template.workgroupsCount = `<text data-route="${RouteName.workgroups}" class="count-text" text-anchor="end" x="250" y="150">{val}</text>`
  template.nodeMenuButton = `<g class="node-menu" transform="matrix(1,0,0,1,230,30)" control-node-menu-id="{id}">
                              <rect x="-4" y="-10" fill="#000000" fill-opacity="0" width="22" height="22"></rect>
                              <circle cx="0" cy="0" r="2"></circle>
                              <circle cx="7" cy="0" r="2"></circle>
                              <circle cx="14" cy="0" r="2"></circle>
                            </g>`
}

export { OrgChartTemplateFactory, DEFAULT_TEMPLATE, SVG_SELECTOR }
