






























































import OrgMenu from '@/components/organization/OrgMenu.vue'
import OrgUnits from '@/components/org-units/OrgUnits.vue'
import OrgHeader from '@/components/organization/OrgHeader.vue'
import SelectOrgMessage from '@/components/organization/SelectOrgMessage.vue'
import LoadingSpinner from '@/components/shared/LoadingSpinner.vue'
import {
  OrganizationMenuNode,
  OrganizationViewModel,
  RenameOrganizationModel
} from '@/models/hierarchy.models'
import { NameDialog, ConfirmDialog } from '@/models/dialog.models'
import { namespace } from 'vuex-class'
import { Component, Vue, Watch } from 'vue-property-decorator'
import {
  ACTION_CLEAR_ORG_UNITS_STATE,
  ACTION_FETCH_ORGS, ACTION_REMOVE_SELECTED_ORG,
  ACTION_SET_SELECTED_ORG_BY_HIERARCHY_CODE
} from '@/store/action-types'
import { OrganizationStatus } from '@/shared/constants/enum'
import NameDialogComponent from '@/components/shared/NameDialogComponent.vue'
import ConfirmDialogComponent from '@/components/shared/ConfirmDialogComponent.vue'
import { RouteName } from '@/router'

const organization = namespace('Organization')
const orgUnits = namespace('OrgUnits')

@Component({
  components: {
    NameDialogComponent,
    OrgHeader,
    OrgMenu,
    OrgUnits,
    SelectOrgMessage,
    LoadingSpinner,
    ConfirmDialogComponent
  }
})
export default class Home extends Vue {
  loading = false
  saving = false
  orgDialog: NameDialog = null
  confirmDialog: ConfirmDialog = null

  @organization.State
  orgs: OrganizationViewModel[]

  @organization.State
  selectedOrg: OrganizationViewModel

  @organization.Getter
  selectedHierarchyId: string

  @organization.Action
  [ACTION_FETCH_ORGS]: () => Promise<OrganizationViewModel[]>

  @organization.Action
  [ACTION_SET_SELECTED_ORG_BY_HIERARCHY_CODE]: (hierarchyId: string) => void

  @organization.Action
  [ACTION_REMOVE_SELECTED_ORG]: () => void

  @orgUnits.Action
  [ACTION_CLEAR_ORG_UNITS_STATE]: () => void

  get orgMenuItems(): OrganizationMenuNode[] {
    if (this.orgs && this.orgs.length && this.orgs.length > 0) {
      return this.lodash.chain(this.orgs)
        .map((value) => {
          return {
            active: value.organizationStatus === OrganizationStatus.Active,
            ...value
          }
        })
        .groupBy('hierarchyType')
        .map((value, key) => {
          return { organizationName: key, children: value }
        })
        .value()
    } else {
      return []
    }
  }

  get hierarchyId(): string | null {
    const { query } = this.$route
    const { hierarchyId } = query
    return hierarchyId as string
  }

  @Watch('hierarchyId', { immediate: false, deep: true })
  onRouteQueryChange(): void {
    this.setSelectedOrgByQueryParam()
  }

  async mounted(): Promise<void> {
    await this[ACTION_FETCH_ORGS]()
    this.setSelectedOrgByQueryParam()
  }

  setSelectedOrgByQueryParam(): void {
    if (this.hierarchyId) {
      this[ACTION_SET_SELECTED_ORG_BY_HIERARCHY_CODE](this.hierarchyId)
    } else {
      this[ACTION_REMOVE_SELECTED_ORG]()
      this[ACTION_CLEAR_ORG_UNITS_STATE]()
    }
  }

  menuItemClick(hierarchyId: string): void {
    this.$router.push({ query: { hierarchyId } })
  }

  cloneOrgDialog(): void {
    const params = {
      editing: false,
      title: `Clone: <strong>${this.selectedOrg.organizationName}</strong>`,
      placeHolderText: 'Hierarchy Name',
      buttonText: 'Clone Hierarchy'
    } as NameDialog
    this.orgDialog = new NameDialog(params)
  }

  renameOrgDialog(): void {
    const params = {
      editing: true,
      title: `Clone: <strong>${this.selectedOrg.organizationName}</strong>`,
      placeHolderText: 'Hierarchy Name',
      buttonText: 'Rename Hierarchy',
      name: `${this.selectedOrg.organizationName}`
    } as NameDialog
    this.orgDialog = new NameDialog(params)
  }

  onOrgDialogSubmit(name: string): void {
    if (!this.orgDialog.editing) {
      this.cloneHierarchy(name)
    } else if (this.orgDialog.editing) {
      this.renameHierarchy(name)
    }
  }

  async cloneHierarchy(name: string): Promise<void> {
    try {
      this.saving = true
      await this.$http.hierarchy.post(
        '/clonehierarchy',
        null,
        {
          params: {
            originalHierarchyId: this.selectedHierarchyId,
            organizationName: name
          }
        }
      )
      this.orgDialog = null
      await this[ACTION_FETCH_ORGS]()
    } catch (e) {
      console.error(e)
    } finally {
      this.saving = false
    }
  }

  async renameHierarchy(name: string): Promise<void> {
    const params = {
      hierarchyId: this.selectedHierarchyId,
      organizationName: name
    } as RenameOrganizationModel
    try {
      this.saving = true
      await this.$http.hierarchy.put(
        '/renameorg',
        params
      )
      this.orgDialog = null
      await this[ACTION_FETCH_ORGS]()
      this.setSelectedOrgByQueryParam()
    } catch (e) {
      console.error(e)
    } finally {
      this.saving = false
    }
  }

  deleteConfirmDialog(): void {
    const params = {
      title: 'Delete Hierarchy',
      text: `Are you sure you want to delete <strong>${this.selectedOrg.organizationName}</strong>?`,
      buttonText: 'Delete Hierarchy',
      buttonColor: 'error'
    } as ConfirmDialog
    this.confirmDialog = new ConfirmDialog(params)
  }

  async deleteHierarchy(): Promise<void> {
    try {
      this.saving = true
      await this.$http.hierarchy.delete(
        '/deactivateorg',
        {
          params: {
            hierarchyId: this.selectedHierarchyId
          }
        }
      )
      this.confirmDialog = null
      await this[ACTION_FETCH_ORGS]()
      await this.$router.push({ name: RouteName.home })
    } catch (e) {
      console.error(e)
    } finally {
      this.saving = false
    }
  }
}
