import Vue from 'vue'
import singleSpaVue from 'single-spa-vue'
import App from './App.vue'
import router from './router'
import store from './store'
import vuetify from './plugins/vuetify'
import { HttpPlugin } from '@/plugins/http'
import { DateFormatPlugin } from '@/plugins/date'
import { OrgChartTemplateFactory } from '@/components/org-chart/orgChart.template.factory'
import VueLodash from 'vue-lodash'
import { chain, groupBy, map, value } from 'lodash'
import './scss/styles.scss'

Vue.config.productionTip = false

Vue.use(HttpPlugin)
Vue.use(DateFormatPlugin)
Vue.use(OrgChartTemplateFactory)
Vue.use(VueLodash, { lodash: { chain, groupBy, map, value } })

const vueLifecycles = singleSpaVue({
  Vue,
  appOptions: {
    render(h: any) {
      return h(App, {
        props: {
          // single-spa props are available on the "this" object. Forward them to your component as needed.
          // https://single-spa.js.org/docs/building-applications#lifecyle-props
          // if you uncomment these, remember to add matching prop definitions for them in your App.vue file.
          /*
          name: this.name,
          mountParcel: this.mountParcel,
          singleSpa: this.singleSpa,
          */
        }
      })
    },
    router,
    store,
    vuetify
  }
})

export const bootstrap = vueLifecycles.bootstrap
export const mount = vueLifecycles.mount
export const unmount = vueLifecycles.unmount
