import _Vue from 'vue'

export interface DateFormats {
  default: (dateString: string) => string
}

const defaultFormat = {
  day: 'numeric', // numeric, 2-digit
  year: 'numeric', // numeric, 2-digit
  month: 'numeric', // numeric, 2-digit, long, short, narrow
  hour: 'numeric', // numeric, 2-digit
  minute: 'numeric' // numeric, 2-digit
}

const dateFormats: DateFormats = {
  default: (dateString: string): string => new Date(dateString).toLocaleString('en-US', defaultFormat)
}

function DateFormatPlugin(Vue: typeof _Vue): void {
  Vue.prototype.$date = dateFormats
}

export { dateFormats as $date, DateFormatPlugin }
