































































import { Component, Vue, Prop } from 'vue-property-decorator'
import { OrganizationMenuNode } from '@/models/hierarchy.models'

@Component({})
export default class OrgMenu extends Vue {
  @Prop() selectedHierarchyId: string
  @Prop({ default: [] }) items: OrganizationMenuNode[]

  search = null

  onSelectionChange(val: string[]): void {
    const hierarchyId = val[0]
    if (hierarchyId && hierarchyId !== this.selectedHierarchyId) {
      this.$emit('menuItemSelected', hierarchyId)
    }
  }
}
