








































































import { Component, Vue, Prop } from 'vue-property-decorator'
import { OrganizationViewModel } from '@/models/hierarchy.models'
import { OrganizationStatus } from '@/shared/constants/enum'

interface OrgHeaderMenuItem {
  title: string
  icon: string
  readonly: boolean
  action: unknown
}

@Component({})
export default class OrgHeader extends Vue {
  @Prop() org: OrganizationViewModel

  items: OrgHeaderMenuItem[] = [
    { title: 'Clone & Edit', icon: 'mdi-content-copy', readonly: true, action: this.clone },
    { title: 'Rename', icon: 'mdi-pencil', readonly: false, action: this.rename },
    { title: 'Delete', icon: 'mdi-trash-can-outline', readonly: false, action: this.delete }
  ]

  get isActive(): boolean {
    return this.org.organizationStatus === OrganizationStatus.Active
  }

  showEditableMenuItem(item: OrgHeaderMenuItem): boolean {
    return item.readonly || (!this.isActive && !item.readonly)
  }

  clone(): void {
    this.$emit('cloneOrg', this.org)
  }

  rename(): void {
    this.$emit('renameOrg', this.org)
  }

  delete(): void {
    this.$emit('deleteOrg', this.org)
  }
}
